<template>
  <div id="uploadImage">
    <div class="main">
      <div class="tt">保密信息</div> 
      <el-form :model="form" :rules="rules" style="margin-top:60px;width:60%" ref="form">
        <el-form-item label="密保问题:" :label-width="formLabelWidth" prop="UserQuestion"> 
            <el-input v-model="form.UserQuestion" clearable></el-input> 
        </el-form-item> 
        <el-form-item label="密码提示答案:" :label-width="formLabelWidth" prop="UserAnswerQuestion"> 
            <el-input v-model="form.UserAnswerQuestion" clearable></el-input> 
        </el-form-item>  
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button :loading="createLoading1" type="primary" @click="comfirmFormHandler('form')">
          <i class="el-icon-check el-icon--left"></i>保 存
        </el-button>
        <el-button @click="resetForm">
          <i class="el-icon-close el-icon--left"></i>重 置
        </el-button>
      </div> 
    </div>
  </div>
</template>

<script>

import http from "../../../../utils/http";
import api from "../../../../utils/api";
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return { 
      createLoading1:false,
      TeacherID:"",
      userid:"",
      username:"",
      formLabelWidth: "160px", 
      form: {
        UserQuestion: "",
        UserAnswerQuestion: ""
      },  
      rules: {}
    };
  },
  computed: {},
  watch: {},
   mounted() {
  let userInfo = http.getUser();
    if (userInfo) {
        this.userid = userInfo.UserID;
        this.username = userInfo.UserName;
      } 
    this.getdata();
 },
  methods: {
    async getdata() {
      const res = await http.get(api.getuserData, {
          id: this.userid,
          Token: http.getToken()
        });
        if (!res.Result) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          });
        } else {
          if(res.Data != null) {  
             this.form.UserQuestion = res.Data.UserQuestion;
             this.form.UserAnswerQuestion = res.Data.UserAnswerQuestion; 
          }
        }
    },
    // 表单确认,添加用户
    comfirmFormHandler(formName) {
      this.$refs[formName].validate(valid => { 
        if (valid) { 
          this.savesubmit();
        } else {
          this.$message({
            showClose: true,
            message: "请输入正确格式",
            type: "error"
          });
          return false;
        }
      });
    },
    savesubmit: async function() {
      this.createLoading1 = true;  
      this.saveData({ 
        id: this.userid,
        question:this.form.UserQuestion,
        answer:this.form.UserAnswerQuestion, 
        Token: http.getToken()
      });
    },
    async saveData(params) {
       const res = await http.post(api.setAnswerQuestion, params);
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.$message({
          showClose: true,
          message: "数据保存成功！",
          type: "success"
        });
      }
      this.createLoading1 = false; 
    },
    resetForm() {
      this.$refs["form"].resetFields();
      this.$refs.form.clearValidate();
    },
  }
};
</script>

<style lang='less'>
#uploadImage {
  text-align: left;
  width: 956px;
  height: 1017px;
  background: rgba(255, 255, 255, 1);
  border-radius: 3px;
  //
  padding: 20px;
  padding-top: 0;
  .tt {
      color: #333;
      font-size: 14px;
      border-bottom: 1px solid #ecf1f7;
      line-height: 50px;
    } 
  .main {  
      .dialog-footer {
        width: 100%;
        display: block;
        text-align: center;
        margin-top: 36px;

    }
  }
}
</style>


